.particles-js-canvas-el {
  z-index: 20;
}
.hero {
  position: relative;
  height: 100vh;
  min-height: 600px;
  display: flex;
  background-color: #e7e7ea;
  overflow: hidden;
  &::before {
    content: "";
    position: absolute;
    width: 800px;
    height: 800px;
    background-color: #f85555;
    transform: rotate(-45deg);

    @media (max-width: 1024px) {
      display: none;
    }
  }
  &::before {
    top: -500px;
    left: -39%;
    z-index: 1001;
    @media (max-width: 1700px) {
      top: -550px;
    }
    @media (max-width: 1600px) {
      top: -600px;
    }
    @media (max-width: 1500px) {
      top: -650px;
    }
    @media (max-width: 1400px) {
      top: -700px;
    }
    @media (max-width: 1300px) {
      top: -750px;
    }
    @media (max-width: 1200px) {
      top: -800px;
    }
    @media (max-width: 1100px) {
      top: -850px;
    }
  }
  &::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 75px;
    bottom: 0;
    left: 0;
    background-color: #d9d9d9;
    z-index: 21;
    @media (max-width: 1200px) {
      height: 50px;
    }
    @media (max-width: 1024px) {
      display: none;
    }
  }
  //&::after {
  // bottom: -5%;
  // right: -20%;
  // z-index: 10;
  // @media (max-width: 1400px) {
  //   bottom: -40%;
  // }
  // @media (max-width: 1200px) {
  //   bottom: -45%;
  // }
  //}
  &__container {
    position: absolute;
    inset: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100%;
    width: 130%;
    z-index: 21;
    // &::after {
    //   content: "";
    //   position: absolute;
    //   width: 100%;
    //   height: 75px;
    //   bottom: 0;
    //   left: 0;
    //   background-color: #d9d9d9;
    //   @media (max-width: 1200px) {
    //     height: 50px;
    //   }
    //   @media (max-width: 1024px) {
    //     display: none;
    //   }
    // }
  }
  &__bg {
    position: absolute;
    inset: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    img {
      position: absolute;
      inset: 0;
      width: 100%;
      height: 100%;
    }
    &::after {
      content: "";
      position: absolute;
      inset: 0;
      background-color: rgba(0, 0, 0, 0.6);
    }
  }

  &__image {
    flex: 1 1 50%;
  }

  &__box {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    flex: 1 1 50%;
  }
  &__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    width: 100%;
    flex: 1 1 auto;
  }

  &__title {
    font-size: 86px;
    color: #fff;
    font-weight: 700;
    @media (max-width: 1024px) {
      font-size: 48px;
    }
    @media (max-width: 576px) {
      font-size: 36px;
    }
  }

  &__descr {
    font-weight: 500;
    font-size: 48px;
    color: #ffe4b5;
    @media (max-width: 1024px) {
      font-size: 32px;
    }
    @media (max-width: 576px) {
      font-size: 24px;
    }
  }

  &__link {
    margin-bottom: 20px;
  }
  &__sci {
    margin: 0 0 110px 0;
    @media (max-width: 1024px) {
      margin: 0 0 60px;
    }
  }

  &__item {
    svg {
      transition: all 0.8s ease 0s;
      fill: rgba(255, 255, 255, 0.6);
    }
    &:hover {
      svg {
        fill: #fff;
      }
    }
  }
}
